.MovieRow{
    margin-bottom: 30px;
}
.movieRow h2{
    margin: 0px 0px 0px 30px
}
.movieRow--listarea{
    overflow: hidden;
    padding-left: 30px;
}
.movieRow--list{
  transition: all ease 0.5s;
}
.movieRow--item{
    display: inline-block;
    width: 150px;
    cursor: pointer;
}
.movieRow--item img{
    width: 100%;
    transform: scale(0.9);
    transition: all ease 0.2s;
}
.movieRow--item img:hover{
    transform: scale(1);
}
.movieRow--left,
.movieRow--rigth{
    position: absolute;
    width: 40px;
    height: 225px;
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.6);
    cursor: pointer;
    opacity: 0;
}
.movieRow--left{
    left: 0;
}
.movieRow--rigth{
    right: 0;
}
.movieRow:hover .movieRow--left,
.movieRow:hover .movieRow--rigth{
    opacity: 1;
    transition: all ease 0.5s;
}
@media(max-width: 760px){
    .movieRow--left,.movieRow--rigth{
        opacity: 1;
    }
}